.search-container{
    height:88px;
}
.searchbox-container{
    width:205px;
    background-color:white;
    margin:0 auto;
    height:55px;
    display:flex;
    align-items: center;
}

.searchbox-container:hover + .material-icons:hover{
    border-radius: 152px;
    color:red;

    
}



.searchbox-input{
    -webkit-appearance:none;
    border:none;
    outline:none;
    padding:10px;
    width:150px;
    line-height:30px;
}

.material-icons{
    margin-left:5%;
}





