@media only screen and (max-width:599px){
    .headerTitle{
      font-size:80px
    }

    .headerImg{
      margin-left:0;
    }

    .material-icons{
      margin-left: 5px;
    }

    .card-list{
        grid-template-columns: 1fr  ;
        margin-top:15px
    }

    .card-container{
      width:70%;
      place-self:center;
    }

    .card-container > p{
      font-size:13px;
    }


    
}
  
 @media only screen and (min-width:600px){
    .headerTitle{
      font-size:85px
    }

    .card-list{
        grid-template-columns: 1fr 1fr ;
    }

    .card-container{
      width:80%
    }

    .serachbox-container{
       width:255px;
    }
  }
  
  @media only screen and (min-width:900px){
    .headerTitle{
      font-size:85px
    }

    .card-list{
        grid-template-columns: 1fr 1fr 1fr ;
    }
  }


  
  @media only screen and (min-width:1200px){
    .card-list{
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }


    .serachbox-input{
      width:100%;
    }

    

  }